//NAVIGATION

.home_logo{
      @include fluid-type(font-size, 20rem, 100rem, 1.333rem, 1.5rem);
      @include fluid-type(padding-top, 20rem, 100rem, 1rem, 2rem);
      letter-spacing:0.5px;
      font-family: 'Helvetica Neue Bold';
      float:left;
      color:#fff;
      position:relative;
      &:hover{

      }
    }

    .navigation{
      position: absolute;
      z-index:2;
      width:100%;
    }

    .right_nav{ 
        @include fluid-type(padding-top, 20rem, 100rem, 1.5rem, 2rem);
        letter-spacing:0.5px;
        float:right;
        right:0;
        
        a{
          @include fluid-type(font-size, 20rem, 100rem, 1rem, 0.75rem);
          font-family: 'Proxima Nova semibold';
          color:#fff; 
          position:relative;
          &:after{
            content:'';
            position:absolute;
            left:0;
            right:0;
            border:1px solid #fff;
            bottom:calc(-1rem);
            transform:scale(0,0);
            transform-origin: left top;
            transition: transform 0.2s ease-out;
          }
            &:hover:after{
            transform:scale(1,1);
          }
        }
    }

