section.projects{
  @include clearfix();
    width:90vw;
    margin:0 5vw;
  
  @include bp(babybear){
    width:99vw;
    margin:0 0.5vw;
  }

   @include bp(papabear){
    width:99.5vw;
    margin:0 0.25vw;
    background-color:#e6e6e6;
  }

  @include bp(grandpabear){
    background-color:transparent;
  }

  .project{
    width:100%;
    float:left;
    position:relative;
    background-size:100%;
    overflow:hidden;
    margin:3vw 0;
    transition: all 200ms ease-out;
    &:focus{outline:none;}

    @include bp(babybear){
        width:49.5%;
        margin:0.25vw 0.25%;
    }
    @include bp(papabear){
        width:33.08%;
        margin:0.125vw 0.125%;
    }
    @include bp(grandpabear){
        width:24.75%;
    }


    .backing_prev{
      width:100%;
      float:left;
    }
    
  }

  .logo_prev{
    position:absolute;
    z-index:2;
    left:50%;
    top:50%;
    transform:translate(-50%,-50%);
    transition: all 400ms ease-out;
  }
  .kaha .logo_prev{width:5rem;}
  .yukfoo .logo_prev{width:7rem;}
  .loaf .logo_prev{width:8rem;}
  .br .logo_prev{width:6rem;}
  
  .overlay{
    opacity:0.5;
    position:absolute;
    top:0;
    bottom:0;
    right:0;
    left:0;
    transition: opacity 300ms ease-out;
    }

  .loaf .overlay{background-color:#000;}
  .yukfoo .overlay{background-color:#C31D17;}
  .br .overlay{background-color:#2E0A2D;}
  .kaha .overlay{background-color:#004997;}

  .project:hover{
    .overlay{
      opacity:0;
    }
    .logo_prev{
      transform:translate(-50%,-50%) scale(1.05,1.05);
      transform-origin: center center;
    }
  }

  .project:active{
    .overlay{background-color:$yellow;}

  }
}





